body {
  margin: 0;
}
.App {
  text-align: center;
  margin: 0;
  padding: 0;
  background:#0F1139;
}

* {
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
}
.card-loader {
    width:100%;
    height: 400px;
}
.main{
    width:80%;
    margin:0 auto;
    position: relative;
}

.purple-shadow1 {
    position:absolute;
    content: "";
    width:300px;
    height: 300px;
    /* background-color: red; */
    left:-250px;
    top:300px;
    background-image: url(../../static/media/purple-blur.e81d0c3a.png);
    background-position: center;
    background-size: contain;
    background-repeat:no-repeat;
}

.products {
    width: 100%;
    margin: 0 auto;
}

.list-items {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    /* grid-template-rows: repeat(4, 1fr); */
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

@media (max-width:1100px) {
    .list-items {
        grid-template-columns: repeat(3, 1fr);
    }
}
@media (max-width:767px) {
    .list-items {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width:640px) {
    .list-items {
        grid-template-columns: repeat(1, 1fr);
    }
}

.cart-item-image {
    width: 50px;
    height: 50px;
    margin-right: 10px;
}


.filters {
    margin:20px 0;
    display: flex;
}

.filters select {
    align-self: flex-start;
    border: none;
    outline: none;
    color:#FFF;
    background:rgba(233, 4, 195, 0.5) ;
    padding:5px 10px;
}
.list-item {
    list-style: none;
    border: 2px solid rgba(233, 4, 195, 0.5);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    flex: 1 1;
    padding: 10px;
    background: linear-gradient(152.97deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0) 100%);
    -webkit-backdrop-filter: blur(42px);
            backdrop-filter: blur(42px);
}

.item-title {
    margin-top: 20px;
    flex: 1 1;
    text-align: center;
    padding: 0 20px;
    color: #fff;
    font-weight: 300;
}


.item-image-block {
    padding: 20px;
    width: 80%;
    height: 50%;
    align-self: center;
}

.item-image {
    width: 100%;
    height: 100%;
}

.add-to-cart-button {
    border: none;
    background: #E904C4;
    border: 1px solid #000;
    color: #fff;
    padding: 10px 0;
    border-radius: 5px;
    transition: all 0.3s;
    cursor: pointer;
}

.add-to-cart-button:hover {
    background: transparent;
    border: 1px solid #E904C4;
}

.item-rating {
    align-self: center;
    width: 100%;
    display: flex;
    justify-content: center;
}

.rating-star {
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 15px;
    height: 15px;
}

.item-price {
    align-self: center;
    margin: 10px 0;
    font-weight: 400;
    color: #fff;
}
.header {
    width: 100%;
    height: 100px;
    background: #0F1139;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid #E904C4;
}

.logo {
    margin-left: 50px;
}

.cart {
    margin-right: 50px;
}

.nav ul,
.login-nav {
    display: flex;
    list-style: none;
    grid-gap: 20px;
    gap: 20px;
}

.nav ul li a,
.login-nav li a {
    position: relative;
    text-decoration: none;
    color: #FFF;
    text-transform: uppercase;
}

.nav ul li a::before,
.login-nav li a::before {
    position: absolute;
    content: "";
    height: 1px;
    width: 0%;
    left: 0;
    bottom: -2px;
    background-color: #E904C4;
    transition: all 0.3s;
}

.nav ul li a:hover,
.login-nav li a:hover {
    color: #E904C4;
}

.nav ul li a:hover::before,
.login-nav li a:hover::before {
    width: 100%;
    color: #E904C4
}

.logo {
    background-image: url(../../static/media/logo.468b4f30.svg);
    background-position: center;
    background-repeat: no-repeat;
    width: 50px;
    height: 50px;
}
.empty-cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap:20px;
    gap:20px;
    font-size:1.2rem;
    font-weight: 500;
}

.empty-cart img {
    width:40px;
}
.cart {
    position: relative;
}

.cart-icon {
    cursor: pointer;
    background-image: url(../../static/media/cart-icon.63fd88d2.svg);
    background-repeat: no-repeat;
    background-position: center;
    width: 25px;
    height: 25px;
}

.cart-counter{
    font-size:10px;
    color: #fff;
    content: "";
    position: absolute;
    right: -10px;
    top: -5px;
    width: 15px;
    height: 15px;
    background: #E904C4;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cart-bar {
    position: absolute;
    width: 400px;
    border: none;
    background-color: #E904C4;
    border-radius: 3px;
    top:40px;
    left: -390px;
    display: flex;
    padding: 20px 10px;
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 0.3s;
    visibility: hidden;
    z-index: 1;
    
}

.cart-bar.visible {
    transition: all 0.3s;
    height: auto;
    overflow: visible;
    color:#fff;
    border:1px solid #0F1139;
    opacity: 1;
    visibility: visible;
    /* display: flex;
    animation: cartpop 0.5s; */

}

