.cart-items-list {
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    padding: 5px;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.cart-scroll {
    max-height: 300px;
    overflow-y: scroll;
}

.cart-item {
    font-size: 0.8rem;
    height: 40px;
    border-radius: 5px;
    background:rgba(15, 17, 57, 0.5);
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all 0.3s;
}

.cart-item:hover {
    background:#0F1139;
}

.product-title {
    width: 80%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: clip;
    flex: 1 1;
    margin-right: 10px;
}

.product-price {
    margin-right: 10px;
    margin-left: 10px;
    flex: 1 1;
}

.empty-cart,
.clear-cart,
.cart-summ {
    align-self: center;
}

.cart-summ {
    align-self: flex-end;
}

.cart-delete-button {
    background-color: transparent;
    border: 1px solid #fff;
    padding: 5px 10px;
    cursor: pointer;
    color:#fff;
    transition: all 0.3s;
}

.cart-delete-button:hover {
    background-color: #E904C4;
    color: #fff;
    border: 1px solid #fff;
}

.cart-nav {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    grid-gap: 20px;
    gap: 20px;
}

.clear-cart,
.confirm-order {
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.clear-cart {
    text-decoration: underline;
}

.clear-cart:hover {
    text-decoration: none;
}

.confirm-order {
    padding: 5px 10px;
    border: 1px solid #000;
    background-color: #000;
    color: #fff;
    transition: all 0.3s;
}

.confirm-order:hover {
    background-color: #fff;
    color: #000;
}

.item-counter {
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    flex: 1 1;
}

.item-counter button {
    width:20px;
    height: 20px;
    cursor: pointer;
    border: 1px solid #fff;
    color:#fff;
    background-color: transparent;
    flex: 1 1;
    transition: all 0.3s;
}

.item-counter button:hover {
    background:#E904C4;
}
.sign-in-button {
    cursor: pointer;
}

.auth-bar {
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

.user-name {
    color: #fff;
}

.sign-in-button,
.log-out-button {
    background: none;
    color:#fff;
    border:2px solid #E904C4;
    padding:5px 10px;
    border-radius: 5px;
    cursor: pointer;
    transition:all 0.3s;
}

.sign-in-button:hover,
.log-out-button:hover {
    background:#E904C4;
}
